/**
 * Global imports
 */
import React from 'react';
import { branch, renderComponent, compose } from 'recompose';

import { Provider, connect } from 'react-redux';
import { createStore } from 'redux';

import SpotifyWebApi from 'spotify-web-api-js';

/**
 * Reducers
 */

const reducer = (state, action) => {
    switch (action.type) {
    case 'ACCESS_TOKEN_CHANGE':
        state.api.setAccessToken(action.accessToken);
        return {
            ...state,
            accessToken: action.accessToken,
            api: state.api,
        };
    default:
        return state;
    }
};

/**
 * Redux
 */

const defaultState = {
    accessToken: null,
    api: new SpotifyWebApi(),
};

const store = createStore(
    reducer,
    defaultState,
    window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
);

const getHashQueryParams = hash =>
    hash
        .substr(1)
        .split('&')
        .reduce((pairs, p) => {
            const [key, value] = p.split('=');
            return {
                ...pairs,
                [key]: value,
            };
        }, {});

const accessToken = getHashQueryParams(window.location.hash).access_token;
if (accessToken) {
    store.dispatch({
        type: 'ACCESS_TOKEN_CHANGE',
        accessToken
    });
}

/**
 * App
 */

const REDIRECT_URI = [
    window.location.protocol,
    '//',
    window.location.host,
    window.location.pathname
].join('');

const LOGIN_URL =
    'https://accounts.spotify.com/authorize'
    + '?client_id=4889def045ee4f4785604263068e69e7'
    + '&response_type=token'
    + `&redirect_uri=${REDIRECT_URI}`
    + '&scope=user-modify-playback-state user-read-playback-state';

const setCheckpoint = api => async () => {
    const state = await api.getMyCurrentPlaybackState();
    localStorage.setItem('playbackState', JSON.stringify(state));
};

const goCheckpoint = api => async () => {
    const state = JSON.parse(localStorage.getItem('playbackState'));
    return api.seek(state.progress_ms, { device_id: state.device.id });
};

const rewind = api => async () => {
    const state = await api.getMyCurrentPlaybackState();
    return api.seek(state.progress_ms - 5000, { device_id: state.device.id });
};

const forward = api => async () => {
    const state = await api.getMyCurrentPlaybackState();
    return api.seek(state.progress_ms + 5000, { device_id: state.device.id });
};

const pause = api => async () => {
    const state = JSON.parse(localStorage.getItem('playbackState'));
    return api.pause({ device_id: state.device.id });
};

const Controls = ({ api }) => (
    <div className="ui container center aligned">
        <div className="huge ui vertical labeled icon buttons">
            <button className="ui labeled icon button" onClick={rewind(api)}>
                <i className="backward icon" />
                Rewind (5 Sec)
            </button>
            <button className="ui labeled icon button" onClick={setCheckpoint(api)}>
                <i className="circle outline icon" />
                Set Checkpoint
            </button>
            <button className="ui labeled icon button blue" onClick={goCheckpoint(api)}>
                <i className="undo alternative icon" />
                Go Checkpoint
            </button>
        </div>
    </div>
);

const Login = () => (
    <div className="ui container center aligned">
        <a className="ui button" href={LOGIN_URL}>Login</a>
    </div>
);

/**
 * App Container
 */

const withLogin = compose(
    connect(({ accessToken }) => ({ isLoggedIn: !!accessToken })),
    branch(
        ({ isLoggedIn }) => !isLoggedIn,
        renderComponent(Login),
    ),
);

const ControlsContainer = connect(({ api }) => ({ api }))(Controls);

const ControlsWithLogin = withLogin(ControlsContainer);

const App = () => (
    <Provider store={store}>
        <ControlsWithLogin />
    </Provider>
);

export default App;



